<template>
  <div class="list" ref="wrapper">
    <div>
      <!-- 当前城市 -->
      <div class="area">
        <div class="title border-bottom">当前城市</div>
        <div class="button-list">
          <div class="button-wrapper">
            <div class="button">{{this.currentCity}}</div>
          </div>
        </div>
      </div>
      <div class="area">
        <!-- 热门城市 -->
        <div class="title border-bottom">热门城市</div>
        <div class="button-list">
          <div class="button-wrapper"
           v-for="item of hotCity" 
           :key="item.id"
           @click="handleCity(item.name)">
            <div class="button">{{item.name}}</div>
          </div>
        </div>
      </div>
      <div class="area" v-for="(item,key) of city" :key="key" :ref="key">
        <!-- 城市首字母列表 -->
          <div class="title border-topbottom" >{{key}}</div>
          <div class="item-list" 
          v-for="ctx of item" 
          :key="ctx.id"
          @click="handleCity(ctx.name)">
            <div class="item border-bottom">{{ctx.name}}</div>
          </div>
      </div>
    </div>
  </div>
</template>
<script>
import Bscroll from 'better-scroll'
import {mapState} from 'vuex'
export default {
  name: "CityList",
  props:{
    hotCity:Array,
    city:Object,
    letter:String
  },
  computed: {
    ...mapState({
      currentCity:'city'
    })
  },
  mounted(){
      this.scroll = new Bscroll(this.$refs.wrapper)

  },
  watch:{
    letter(){
      if(this.letter){
        const element = this.$refs[this.letter][0]
        this.scroll.scrollToElement(element)
      }
    }

  },
  methods:{
    handleCity(city){
      this.$store.dispatch('changeCity',city)
        this.$router.push('/')
    }
  }
};
</script>
<style lang="stylus" scoped>
@import '~styles/varibles.styl';

.border-bottom {
  &:after {
    border-color: #ccc;
  }

  &:before {
    border-color: #ccc;
  }
}

.border-topbottom {
  &:after {
    border-color: #ccc;
  }

  &:before {
    border-color: #ccc;
  }
}

.list {
  position: absolute;
  left: 0;
  right: 0;
  top: 1.58rem;
  bottom: 0;
  overflow: hidden;

  .title {
    line-height: 0.6rem;
    height: 0.6rem;
    background: #eee;
    padding-left: 0.2rem;
    font-size: 0.26rem;
  }

  .button-list {
    overflow: hidden;
    padding: 0.1rem 0.6rem 0.1rem 0.1rem;

    .button-wrapper {
      width: 33.33%;
      float: left;

      .button {
        margin: 0.1rem;
        padding: 0.1rem 0;
        text-align: center;
        border: 0.02rem solid #ccc;
        border-radius: 0.06rem;
      }
    }
  }

  .item-list {
    .item {
      line-height: 0.76rem;
      padding-left: 0.2rem;
    }
  }
}
</style>


